import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

const baseUrl = 'http://localhost:82/Qlever%20Solutions/public/api';
const store = new Vuex.Store({
    state: {
        data: {},
        authUser: JSON.parse( localStorage.getItem('user') ),
        infoBarShow: false,
        infoBarText: '',
        infoBarType: '',
    },
    getters: {
        authData(state) {
          return state.authUser;
        },
        data(state) {
            return state.data;
        },
        infoBarShow(state) {
            return state.infoBarShow;
        },
        infoBarText(state) {
            return state.infoBarText;
        },
        infoBarType(state) {
            return state.infoBarType;
        }
    },
    mutations: {
        set(state, { type, items }) {
            state[type] = items;
        }
    },
    actions: {
        queryGet({commit}, { query, answerQuery = '', text = '', answer = false }) {
            const url = `${baseUrl}${query}?token=${localStorage.getItem('token')}`;
            const answerUrl = `${baseUrl}${answerQuery}?token=${localStorage.getItem('token')}`;
            axios.get(url)
                .then( response => {
                    if ( typeof response.data !== 'string') {
                        commit('set', {type: 'data', items: response.data});
                    }
                    return response;
                })
                .then( response => {
                    if ( text.length ) {
                        if ( typeof response.data === 'string') {
                            store.dispatch('showInfoBar', { text: response.data, type: 'success' });
                        } else {
                            store.dispatch('showInfoBar', { text, type: 'success' });
                        }
                    }
                })
                .then( () => {
                    if ( answer ) {
                        axios.get(answerUrl)
                            .then( response => {
                                if ( typeof response.data !== 'string') {
                                    commit('set', {type: 'data', items: response.data});
                                }
                            })
                    }
                })
                .catch( error =>
                    store.dispatch('showInfoBar', {
                        text:'Произошла ошибка, попробуйте ещё раз!',
                        type: 'error'
                    })
                );
        },
        queryPost({ commit }, { query, data, text = '' } ) {
            const url = `${baseUrl}${query}`;
            axios.post(url, data)
                .then( response => {
                    store.dispatch('showInfoBar', {
                        text: response.data,
                        type: 'success'
                    })
                })
                .then( () => {
                    axios.get(url)
                    .then( (response) => {
                        if ( typeof response.data !== 'string') {
                            commit('set', {type: 'data', items: response.data});
                        }
                    });
                })
                .catch(error =>
                    store.dispatch('showInfoBar', {
                        text:'Произошла ошибка, попробуйте ещё раз!',
                        type: 'error'
                    })
                );
        },
        showInfoBar({ commit }, {text, type}) {
            if (text.length) {
                commit('set', {type: 'infoBarShow', items: true});
                commit('set', {type: 'infoBarText', items: text});
                commit('set', {type: 'infoBarType', items: type});
                setTimeout( () => commit('set', {type: 'infoBarShow', items: false}), 2000);
            }
        }
    }
});

export default store;


